body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Roboto Condensed', sans-serif;
  background: #070808;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  text-align: center;
  color: #76e0b4;
  display: grid;
  grid-template-columns: 80% 1fr 1fr;
  align-items: center;
}

.header h1 {
  margin: .5%;
  letter-spacing: .2vw;
  justify-self: start;
}

.header a {
  color: #76e0b4;
}

.marker-btn {
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
}

.marker-btn img {
  width: 22px;
  height: 22px;
}

/* Header Layout */
@media only screen and (max-width: 600px) {
  .header h1{
    font-size: 1.7vh;
    margin: 2%;
  }

  .header a {
    font-size: 1.5vh;
  }
}
